import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../Home.vue';
import Formulaire1 from '../components/Formulaire1.vue';
import Formulaire2 from '../components/Formulaire2.vue';
import Formulaire3 from '../components/Formulaire3.vue';
import Formulaire4 from '../components/Formulaire4.vue';
import Formulaire5 from '../components/Formulaire5.vue';

Vue.use(VueRouter);

const routes = [
    {
        path: '/',
        name: 'Home',
        component: Home,
    },
    {
        path:'/Formulaire1',
        name: 'Formulaire1',
        component: Formulaire1,
    },
    {
        path: '/Formulaire2',
        name: 'Formulaire2',
        component: Formulaire2,
    },
    {
        path: '/Formulaire3',
        name: 'Formulaire3',
        component: Formulaire3,
    },
    {
        path: '/Formulaire4',
        name: 'Formulaire4',
        component: Formulaire4,
    },
    {
        path: '/Formulaire5',
        name: 'Formulaire5',
        component : Formulaire5,
    }
];
const router = new VueRouter({
    routes,
});

export default router;