<template>
  <div id="app" class="container">
      <form class="my-form">
        <legend>Ajouter un chercheur :</legend>
        <br />
        <div class="input-div">
          <label>Nom : </label
          ><input
            v-model="chercheur.nom"
            class="input"
            placeholder="Nom"
          />
        </div>
        <div class="input-div">
          <label>Prenom : </label
          ><input
            v-model="chercheur.prenom"
            class="input"
            placeholder="Prenom"
          />
        </div>

        <div class="input-div">
          <label>URL Page Web : </label
          ><input
            v-model="chercheur.urlPageWeb"
            class="input"
            placeholder="URL Page Web"
          />
        </div>

        <div class="input-div" v-if="checkImage()">
          <label>Photo de Profil : </label><br /><img
            :src="chercheur.imageDeProfil"
            fluid
            class="img-profil"
          /><br /><br /><input type="file" @change="getphoto" />
        </div>
        <div class="input-div" v-if="!checkImage()">
          <label>Photo de Profil : </label><br /><img
            src="../photo-de-profil.jpg"
            fluid
            class="img-profil"
          /><br /><br /><input type="file" @change="getphoto" />
        </div>

        <div v-if="checkForm()">
          <v-btn class="button" @click="addToChercheurArray(chercheur)"> Ajouter </v-btn>
        </div>
        <div v-if="!checkForm()">
          <v-btn class="button" disabled @click="addToChercheurArray(chercheur)">
            Ajouter
          </v-btn>
        </div>
      </form>
      <br />
    </div>
</template>
  
  <script>
export default {
  data() {
    return {
      chercheurs: [],
      chercheur: {
        nom: undefined,
        prenom: undefined,
        urlPageWeb: undefined,
        imageDeProfil: undefined,
      },
    };
  },
  metaInfo() {
    return {
      title: "Ajouter un Chercheur",
    };
  },
  methods: {
    addToChercheurArray: function (chercheurToAdd) {
      let chercheur = new Object();
      (chercheur.nom = chercheurToAdd.nom),
        (chercheur.prenom = chercheurToAdd.prenom),
        (chercheur.urlPageWeb = chercheurToAdd.urlPageWeb),
        (chercheur.imageDeProfil = chercheurToAdd.imageDeProfil),
        this.chercheurs.push({
          chercheur,
        });
      this.chercheur.nom = undefined;
      this.chercheur.prenom = undefined;
      this.chercheur.urlPageWeb = undefined;
      this.chercheur.imageDeProfil = undefined;
    },
    checkForm: function () {
      if (!this.chercheur.nom) {
        return false;
      } else if (!this.chercheur.prenom) {
        return false;
      } else {
        return true;
      }
    },
    checkImage() {
      if (this.chercheur.imageDeProfil) {
        return true;
      }
      return false;
    },
    getphoto(event) {
      this.chercheur.imageDeProfil = URL.createObjectURL(event.target.files[0]);
    },
  },
};
</script>
  
  <style>
legend{
  font-size:2.9rem;
  margin:15px;
}
.my-form {
  width: 100%;
  height:100%;
  margin: auto;
  background-image: linear-gradient(to bottom, 		palegoldenrod, lightgoldenrodyellow,	palegoldenrod);
  border-radius: 30px;
  padding: 20px;
  display:flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.input-div{
  display:flex;
  flex-direction: column;
  margin-bottom:30px;
  width:40%;
  border-bottom: 2px solid #ffde4a;
}

.input-div > label{
  transform: translateY(-50%);
  font-size:18px;
  transition: 0.3s;
}

.input {
  border:none;
  outline:none;
}

.button {
  background-image: linear-gradient(to right, goldenrod,	#FFD700);
  cursor:pointer;
}
