<template>
  <div id="app" class="container">
      <form class="my-form">
        <legend>Ajouter une publication:</legend>
        <br />
        <div class="input-div">
          <label>Titre : </label>
          <input class="input" v-model="publication.titre" />
        </div>
        <div class="input-div">
          <label>Résumé : </label>
          <textarea class="input" v-model="publication.resume" />
        </div>
        <div class="input-div">
          <label>Photo de la Publication : </label><br /><img
            :src="publication.photo"
            fluid
            class="img-photo"
            v-if="checkImage()"
          />
          <img
            src="../livre-manquant.png"
            fluid
            class="img-photo"
            v-if="!checkImage()"
          /><br /><br /><input type="file" @change="getphoto" />
        </div>

        <div class="input-div">
          <label>Ouvrage : </label>
          <select
            class="round"
            v-model="publication.ouvrage"
          >
            <option>Ouvrage 1</option>
          </select>
        </div>

        <div class="input-div">
          <label>Pages</label>
          <input
            type="number"
            min="0"
            max="3000"
            placeholder="Nombre de pages"
            class="input"
            v-model="publication.pages"
          />
        </div>

        <div class="input-div">
          <label>Numéro :</label>
          <input
            type="number"
            min="0"
            max="3000"
            placeholder="Numéro"
            class="input"
            v-model="publication.numero"
          />
        </div>

        <div class="input-div">
          <label>Volume :</label>
          <input
            type="number"
            min="0"
            max="3000"
            placeholder="Volume"
            class="input"
            v-model="publication.volume"
          />
        </div>

        <div class="input-div">
          <label>Lien article :</label>

          <input
            type="text"
            class="input"
            v-model="publication.urlArticle"
          />
        </div>

        <div v-if="checkForm()">
          <v-btn class="button" @click="addToPublicationsArray(publication)"> Ajouter </v-btn>
        </div>
        <div v-if="!checkForm()">
          <v-btn class="button" disabled @click="addToPublicationsArray(publication)">
            Ajouter
          </v-btn>
        </div>
      </form>
    </div>
</template>
  
  <script>
export default {
  data() {
    return {
      publications: [],
      publication: {
        titre: undefined,
        resume: undefined,
        photo: undefined,
        ouvrage: undefined,
        pages: undefined,
        numero: undefined,
        volume: undefined,
        urlArticle: undefined,
      },
    };
  },
  metaInfo() {
    return {
      title: "Ajouter une Publication",
    };
  },
  methods: {
    addToPublicationsArray: function (publicationToAdd) {
      let pub = new Object();
      pub.titre = publicationToAdd.titre;
      pub.resume = publicationToAdd.resume;
      pub.photo = publicationToAdd.photo;
      pub.ouvrage = publicationToAdd.ouvrage;
      pub.pages = publicationToAdd.pages;
      pub.numero = publicationToAdd.numero;
      pub.volume = publicationToAdd.volume;
      pub.urlArticle = publicationToAdd.urlArticle;
      this.publications.push({
        pub,
      });
      this.publication.titre = undefined;
      this.publication.resume = undefined;
      this.publication.photo = undefined;
      this.publication.ouvrage = undefined;
      this.publication.pages = undefined;
      this.publication.numero = undefined;
      this.publication.volume = undefined;
      this.publication.urlArticle = undefined;
    },
    checkForm: function () {
      if (!this.publication.titre) {
        return false;
      } else if (!this.publication.resume) {
        return false;
      } else if (!this.publications.photo) {
        return false;
      } else if (!this.publications.ouvrage) {
        return false;
      } else if (!this.publications.pages) {
        return false;
      } else if (!this.publications.numero) {
        return false;
      } else if (!this.publications.volume) {
        return false;
      } else if (!this.publications.urlArticle) {
        return false;
      } else {
        return true;
      }
    },
    checkImage() {
      if (this.publication.photo) {
        return true;
      }
      return false;
    },
    getphoto(event) {
      this.publication.photo = URL.createObjectURL(event.target.files[0]);
    },
  },
};
</script>
  
  <style>
legend{
  font-size:2.9rem;
  margin:15px;
}
.my-form {
  width: 100%;
  height:100%;
  margin: auto;
  background-image: linear-gradient(to bottom, 		palegoldenrod, lightgoldenrodyellow,	palegoldenrod);
  border-radius: 30px;
  padding: 20px;
  display:flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.input-div{
  display:flex;
  flex-direction: column;
  margin-bottom:30px;
  width:40%;
  border-bottom: 2px solid #ffde4a;
}

.input-div > label{
  transform: translateY(-50%);
  font-size:18px;
  transition: 0.3s;
}

.input {
  border:none;
  outline:none;
}

.button {
  background-image: linear-gradient(to right, goldenrod,	#FFD700);
  cursor:pointer;
}
.img-photo {
  width: 200px;
  height: 200px;
}
</style>