<template>
  <div id="app" class="container">
      <form class="my-form">
        <legend>Ajouter un système:</legend>
        <div class="input-div">
          <label>Nom du système : </label>
          <input class="input" v-model="systeme.nom" />
        </div>
        
        <div class="input-div">
          <label>Description : </label>
          <textarea class="input" v-model="systeme.desc" />
        </div>

        <div v-if="checkForm()">
          <v-btn class="button" @click="addToSystemesArray(systeme)"> Ajouter </v-btn>
        </div>
        <div v-if="!checkForm()">
          <v-btn class="button" disabled @click="addToSystemesArray(systeme)"> Ajouter </v-btn>
        </div>
      </form>
  </div>
</template>
  
  <script>
export default {
  data() {
    return {
      systemes: [],
      systeme: {
        nom: undefined,
        desc: undefined,
      },
    };
  },
  metaInfo() {
    return {
      title: "Ajouter un Système",
    };
  },
  methods: {
    addToSystemesArray: function (systemeToAdd) {
      let sys = new Object();
      sys.nom = systemeToAdd.nom;
      sys.desc = systemeToAdd.desc;
      this.ouvrages.push({
        sys,
      });
      this.systeme.nom = undefined;
      this.systeme.desc = undefined;
    },
    checkForm: function () {
      if (!this.systeme.nom) {
        return false;
      } else if (!this.systeme.desc) {
        return false;
      } else {
        return true;
      }
    },
  },
};
</script>
  
