<template>
  <div id="app" class="container">
      <form class="my-form">
        <legend>Ajouter un nom d'ouvrage:</legend>
        <br />
        <div class="input-div">
          <label>Nom de l'ouvrage : </label
          ><input
            v-model="ouvrage.nomOuvrage"
            class="input"
            placeholder="Nom de l'ouvrage"
          />
        </div>
        <div class="input-div">
          <label>Acronyme : </label
          ><input
            v-model="ouvrage.acronyme"
            class="input"
            placeholder="Acronyme"
          />
        </div>
        <div class="input-div">
          <label>Type : </label>
          <select
            class="round"
            v-model="ouvrage.type"
            placeholder="Type de l'ouvrage"
          >
            <option>Conférence</option>
            <option>Journal</option>
            <option>Livre</option>
          </select>
        </div>

        <div v-if="checkForm()">
          <v-btn class="button" @click="addToOuvrageArray(ouvrage)"> Ajouter </v-btn>
        </div>
        <div v-if="!checkForm()">
          <v-btn class="button" disabled @click="addToOuvrageArray(ouvrage)"> Ajouter </v-btn>
        </div>
      </form>
      <br />
    </div>
</template>
  
  <script>
export default {
  data() {
    return {
      ouvrages: [],
      ouvrage: {
        nomOuvrage: undefined,
        acronyme: undefined,
        type: undefined,
      },
    };
  },
  metaInfo() {
    return {
      title: "Ajouter un nom d'ouvrage",
    };
  },
  methods: {
    addToOuvrageArray: function (ouvrageToAdd) {
      let ouvrage = new Object();
      ouvrage.nomOuvrage = ouvrageToAdd.nomOuvrage;
      ouvrage.acronyme = ouvrageToAdd.acronyme;
      ouvrage.type = ouvrageToAdd.type;
      this.ouvrages.push({
        ouvrage,
      });
      this.ouvrage.nomOuvrage = undefined;
      this.ouvrage.acronyme = undefined;
      this.ouvrage.type = undefined;
    },
    checkForm: function () {
      if (!this.ouvrage.nomOuvrage) {
        return false;
      } else if (!this.ouvrage.acronyme) {
        return false;
      } else {
        return true;
      }
    },
  },
};
</script>
  
  <style>
legend{
  font-size:2.9rem;
  margin:15px;
}
.my-form {
  width: 100%;
