  <nav
    class="navigation"
    name="navbar"
    @mouseout="menuout()"
    @mouseover="menuclick()"
  >
    <div class="img-menu-and-text">
      <img src="../menu-icon.png" class="img-menu" />
      <span class="text-menu">MENU</span>
    </div>
  </nav>
