<style>
.img-menu-and-text {
  display: flex;
  flex-direction: column;
}

.menu-option > div:hover {
  text-decoration: none;
  background-color: #ffd700;
  color: #fafad2;
}
.menu-option {
  opacity: 0;
  text-align: center;
  height: 100%;
}
.menu-option > div {
  transition-property: color background-color;
  transition-duration: 0.5s;
  height: 100%;
  display: flex;
}

.menu-option > div > span {
  margin: auto;
  text-align: center;
}
.navigation {
  opacity: 1;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
a {
  text-decoration: none;
}
.img-menu-and-text > .img-menu {
  margin-left: 10px;
  width: 50px;
  transform: rotate(-90deg);
  transition-property: transform;
  transition-duration: 0.5s;
}
.img-menu-and-text > .img-menu:hover {
  transform: rotate(0deg);
}
.img-menu-and-text > .img-menu:hover + .text-menu {
  opacity: 1;
}
.img-menu-and-text > .text-menu {
  margin-left: 10px;
  opacity: 0;
  transition-property: opacity;
  transition-duration: 0.5s;
}
</style>

<script>
export default {
  methods: {
    menuclick() {
      let imgMenu = document.querySelector(".img-menu-and-text");
      imgMenu.addEventListener("click", function () {
        setTimeout(function () {
          imgMenu.style.transitionProperty = "opacity";
          imgMenu.style.transitionDuration = "0.4s";
          imgMenu.style.opacity = "0";
        }, 400);

        setTimeout(function () {
          let navi = document.querySelector(".navigation");
          const menu =
            '<a class="menu-option" style="text-decoration: none; color: inherit;" href="#/"><div><span>Accueil</span></div></a>\
    <a class="menu-option" style="text-decoration: none; color: inherit;" href="#/Formulaire1"><div><span>Formulaire 1</span></div></a>\
    <a class="menu-option" style="text-decoration: none; color: inherit;" href="#/Formulaire2"><div><span>Formulaire 2</span></div></a>\
    <a class="menu-option" style="text-decoration: none; color: inherit;" href="#/Formulaire3"><div><span>Formulaire 3</span></div></a>\
    <a class="menu-option" style="text-decoration: none; color: inherit;" href="#/Formulaire4"><div><span>Formulaire 4</span></div></a>\
    <a class="menu-option" style="text-decoration: none; color: inherit;" href="#/Formulaire5"><div><span>Formulaire 5</span></div></a>';
          navi.innerHTML = menu;
          let menuOption = document.querySelectorAll(".menu-option");
          for (let i = 0; i < menuOption.length; i++) {
            menuOption[i].style.transitionProperty = "opacity";
            menuOption[i].style.transitionDuration = "0.4s";
            menuOption[i].style.opacity = "1";
          }
        }, 800);
      });
    },
    menuout() {
      let navout = document.querySelector(".tdmain");
      navout.addEventListener("click", function () {
        setTimeout(function () {
          let menuOption = document.querySelectorAll(".menu-option");
          for (let i = 0; i < menuOption.length; i++) {
            menuOption[i].style.transitionProperty = "opacity";
            menuOption[i].style.transitionDuration = "0.4s";
            menuOption[i].style.opacity = "0";
          }
        }, 400);
        setTimeout(function () {
          let navi = document.querySelector(".navigation");
          const menuOut = '<div class="img-menu-and-text">\
        <img src="data:image/png;base64," \
        class="img-menu">\
            <span class="text-menu">MENU</span></div>';
          navi.innerHTML = menuOut;
          let imgMenu = document.querySelector(".img-menu-and-menu");
          imgMenu.style.transitionProperty = "opacity";
          imgMenu.style.transitionDuration = "0.4s";
          imgMenu.style.opacity = "1";
        }, 800);
      });
    },
  },
};
</script>