<template>
  <div id="app" class="container">
    <form class="my-form">
      <legend>Ajouter un ouvrage:</legend>
      <br />
      <div class="input-div">
        <label>Nom de l'ouvrage : </label>
        <select class="round" v-model="ouvrage.nomOuvrage">
          <option>Ouvrage 1</option>
        </select>
      </div>
      <div class="input-div">
        <label>Annee : </label
        >
        <input type="number" min="1900" max="2099" step="1" value="2016" class="input" v-model="ouvrage.annee"/>
      </div>
      <div class="input-div">
          <label>Lieu : </label>
          <input class="input" placeholder="Lieu" v-model="ouvrage.lieu">
      </div>

      <div v-if="checkForm()">
        <v-btn class="button" @click="addToOuvragesArray(ouvrage)"> Ajouter </v-btn>
      </div>
      <div v-if="!checkForm()">
        <v-btn class="button" disabled @click="addToOuvragesArray(ouvrage)"> Ajouter </v-btn>
      </div>
    </form></div>
</template>
  
  <script>
export default {
  data() {
    return {
      ouvrages: [],
      ouvrage: {
        nomOuvrage: undefined,
        annee: undefined,
        lieu:undefined,
      },
    };
  },
metaInfo() {
    return {
      title: "Ajouter un Ouvrage",
    };
  },
  methods: {
    addToOuvragesArray: function (ouvrageToAdd) {
      let ouvrage = new Object();
      ouvrage.nomOuvrage = ouvrageToAdd.nomOuvrage;
      ouvrage.annee = ouvrageToAdd.annee;
      ouvrage.lieu = ouvrageToAdd.lieu;
      this.ouvrages.push({
        ouvrage,
      });
      this.ouvrage.nomOuvrage = undefined;
      this.ouvrage.annee = undefined;
      this.ouvrage.lieu = undefined;
    },
    checkForm: function () {
      if (!this.ouvrage.nomOuvrage) {
        return false;
      } else if (!this.ouvrage.annee) {
        return false;
      } 
      else if(!this.ouvrage.lieu){
          return false;
      } else {
        return true;
      }
    },
  },
};
</script>
  
  <style>
