  margin: auto;
  background-image: linear-gradient(to bottom, 		palegoldenrod, lightgoldenrodyellow,	palegoldenrod);
  border-radius: 30px;
  padding: 20px;
  display:flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.input-div{
  display:flex;
  flex-direction: column;
  margin-bottom:30px;
  width:40%;
  border-bottom: 2px solid #ffde4a;
}

.input-div > label{
  transform: translateY(-50%);
  font-size:18px;
  transition: 0.3s;
}

.input {
  border:none;
  outline:none;
}

.button {
  background-image: linear-gradient(to right, goldenrod,	#FFD700);
  cursor:pointer;
}

select{
  border:none;
}

